.history {
  padding: 4rem 0 8rem;

  &--pl {
    padding-left: 14rem;

    @include lg {
      padding-left: 0;
    }
  }

  &__head {}

  &__head-overhead {
    color: #070707;
    margin: 0 0 .7rem;
    text-transform: uppercase;
  }

  &__head-name {
    font-size: 5.2rem;
    margin: 0 0 0.8rem;

    @include md {
      font-size: 42px;
    }

    @include sm {
      font-size: 32px;
    }

    @include xs {
      font-size: 28px;
    }
  }

  &__head-subname {
    font-size: 1.8rem;
    margin: 0 0 1.5rem;
    @include md {
      font-size: 16px;
    }

    @include sm {
      font-size: 15px;
    }
  }

  &__head-info {
    font-size: 1.8rem;
    line-height: 1.2;
    margin: 0 0 2.5rem;

    @include md {
      font-size: 16px;
      margin: 0 0 30px;
    }

    @include sm {
      font-size: 15px;
    }
  }

  &__head-video {
    max-width: 85.5rem;
    height: 44rem;
    margin: 0 0 1.9rem;
    position: relative;

    @include md {
      height: 350px;
      margin: 0 0 30px;
    }

    @include xs {
      height: 276px;
    }

    iframe {
      position: relative;
      z-index: 1;
      width: 100%;
      height: 100%;
    }
  }

  &__head-video-preload {
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 1;
    cursor: pointer;

    &:hover {
      .history__head-video-play {
        transform: translate(-50%, -50%) scale(1.2);
      }
    }
  }

  &__head-video-poster {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__head-video-play {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1);
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 5.5rem 0 5.5rem 9.6rem;
    border-color: transparent transparent transparent $colorWhite;
    transition: transform .6s;
    cursor: pointer;

    @include md {
      border-width: 3.5rem 0 3.5rem 6rem;
    }
  }

  &__head-video-loader {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &__head-btn {
    min-height: 7.4rem;
    background: $colorWhite;
  }

  &__gallery {
    padding: 2rem 0 7rem;

    @include lg {
      padding: 40px 0 30px;
    }
  }

  &__gallery-slider {
    margin: 0 0 3rem;

    .swiper-slide {
      width: auto;

      @media (min-width: 1700px) {
        &:first-of-type {
          margin-left: calc((100vw - 171rem) / 2);
        }

        &:last-of-type {
          margin-right: calc((100vw - 171rem) / 2);
        }
      }

      @media (max-width: 1700px) {
        &:first-of-type {
          margin-left: calc((100vw - 136rem) / 2);
        }

        &:last-of-type {
          margin-right: calc((100vw - 136rem) / 2);
        }
      }

      @include lg {
        &:first-of-type {
          margin-left: 25px;
        }

        &:last-of-type {
          margin-right: 25px;
        }
      }
    }
  }

  &__slide {
    height: 32.8rem;
    cursor: pointer;

    @include md {
      height: 270px;
    }

    @include sm {
      height: 230px;
    }

    @include xs {
      height: 196px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__scroll {
    max-width: 107rem;

    @media (min-width: 1700px) {
      max-width: 139rem;
    }

  }

  &__info {
    max-width: 91rem;
    border-left: .1rem solid $colorBlack;
    margin: 0 0 6rem;

    @include lg {
      border-left: none;
      margin: 0 0 40px;
    }

    @include xs {
      margin: 0 0 30px;
    }

    p {
      &:not(:last-of-type) {
        margin: 0 0 1.6rem;
      }
    }
  }

  &__locations {
    margin: 0 0 6rem;

    @include lg {
      margin: 0 0 40px;
    }

    @include xs {
      margin: 0 0 30px;
    }

    p {
      &:not(:last-of-type) {
        margin: 0 0 1.6rem;
      }
    }

    a {
      color: $colorBlack;

      &:hover {
        text-decoration: underline;
      }
    }
  }

  &__locations-title {
    margin: 0 0 2rem;
  }

  &__locations-list {

    li {
      list-style-position: inside;
      list-style-type: decimal;
      margin: 0 0 1.6rem;
    }
  }

  &__help {
    border: .8rem solid #D0C795;
    background: $colorWhite;
    padding: 6rem 7rem 7rem;
    margin: 0 0 0 5.7rem;

    @include lg {
      padding: 40px 30px 50px;
      margin: 0 -40px;
    }
  }

  &__help-title {}

  &__help-info {
    margin: 0 0 2rem;

    @include lg {
      font-size: 18px;
    }
  }

  &__help-form {}

  &__help-checkbox {
    display: block;
    cursor: pointer;

    &:not(:last-of-type) {
      margin: 0 0 1.2rem;

      @include md {
        margin: 0 0 25px;
      }
    }

    &:hover {
      .history__help-checkbox-el {
        border-color: #D0C795;
      }
    }

    input {

      &:checked+.history__help-checkbox-wrap .history__help-checkbox-el::after {
        opacity: 1;
      }
    }
  }

  &__help-checkbox-wrap {
    display: flex;
    font-size: 1.8rem;

    @include md {
      font-size: 16px;
    }

    @include sm {
      font-size: 15px;
    }
  }

  &__help-checkbox-el {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 1.8rem;
    height: 1.8rem;
    flex-shrink: 0;
    border: .1rem solid #000000;
    margin: .5rem 1.8rem 0 0;
    transition: border .3s;

    &::after {
      content: 'X';
      font-size: 1.6rem;
      opacity: 0;
      transition: opacity .3s;
    }
  }

  &__help-link {
    color: $colorBlack;
    text-decoration: underline;

    &:hover {
      text-decoration: none;
    }
  }

  &__help-form-btn {
    min-height: 7.5rem;
    font-size: 2.4rem;
    text-transform: uppercase;
    background: $colorBlack;
    margin: 3.5rem 0 3rem;
    padding: 0 5rem;
    color: $colorWhite;
    border: none;
    cursor: pointer;
    transition: background .3s;

    &:hover {
      background: rgba($color: $colorBlack, $alpha: .8);
    }

    @include lg {
      min-height: 57px;
      font-size: 20px;
      margin: 40px 0 25px;
    }
  }

  &__help-footer {
    font-size: 1.8rem;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include md {
      flex-direction: column-reverse;
      align-items: flex-start;
      font-size: 16px;
    }

    @include sm {
      font-size: 15px;
    }
  }

  &__help-footer-link {
    margin: 0 2rem 0 0;

    @include md {
      margin: 20px 0 0;
    }
  }

  &__help-footer-pay {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin: 0 -1rem -0.2rem;

    @include md {
      padding: 0 -4px -2px;
    }
  }

  &__help-footer-pay-item {
    padding: 0 1rem 0.2rem;

    @include md {
      padding: 0 4px 2px;

      img {
        max-width: 56px;
      }
    }
  }
}
