.home-about {
  padding: 0 0 17rem;

  @include lg {
    padding: 0 0 80px;
  }

  @include xs {
    padding: 0 0 50px;
  }

  &__container {
    max-width: 119rem;
    width: 100%;
    margin: 0 auto;

    @media (max-width: 1700px) {
      max-width: 90rem;
    }

    @include lg {
      padding: 0 30px;
    }
  }

  &__title {
    margin: 0 0 8rem;

    @include lg {
      margin: 0 0 50px;
    }

    @include xs {
      font-size: 32px;
      margin: 0 0 30px;
    }
  }

  &__text {
    display: flex;
    justify-content: flex-end;
    font-size: 1.8rem;
    margin: 0 -10rem;

    p {
      &:not(:last-of-type) {
        margin: 0 0 1.6rem;
      }
    }

    b {
      font-weight: 500;
    }

    @include lg {
      margin: 0 -20px;
    }

    @include md {
      font-size: 16px;
    }

    @include sm {
      display: block;
      font-size: 15px;
      margin: 0;
    }
  }

  &__text-col {
    max-width: 62.7rem;
    flex: 0 1 50%;
    padding: 0 10rem;

    @include lg {
      padding: 0 20px;
    }

    @include sm {
      padding: 0;

      &:first-of-type {
        margin: 0 0 50px;
      }
    }

  }
}
