@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Bodoni";

  font-display: swap;
  src:
    url("../fonts/bodoni/bodoni-cyrillic.woff2") format("woff2"),
    url("../fonts/bodoni/bodoni-cyrillic.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Roboto";

  font-display: swap;
  src:
    url("../fonts/roboto/roboto-v30-latin_cyrillic-300.woff2") format("woff2"),
    url("../fonts/roboto/roboto-v30-latin_cyrillic-300.woff") format("woff");
}
