.faq {
  padding: 0 0 10rem;

  @include lg {
    padding: 0 0 90px;
  }

  @include xs {
    padding: 0 0 60px;
  }

  &__title {
    text-align: center;
    margin: 0 0 5rem;

    @include lg {
      margin: 0 0 30px;
    }

    @include xs {
      margin: 0 0 20px;
    }
  }

  &__item {
    &:not(:last-of-type) {
      .faq__question {
        border-bottom: .1rem solid #000000;
      }
    }

    &.open {

      .faq__question {
        border-color: transparent;

        &::after {
          transform: rotate(-90deg);
        }
      }

      .faq__answer {
        padding-top: 2.8rem;
        padding-bottom: 2.8rem;
        opacity: 1;
        box-sizing: content-box;

        @include lg {
          padding-top: 20px;
          padding-bottom: 20px;
        }
      }
    }
  }

  &__question {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-family: $font-family-bodoni;
    font-size: 3.2rem;
    font-weight: 400;
    line-height: 1.2;
    text-transform: uppercase;
    text-align: left;
    width: calc(100% - 40px);
    padding: 3.4rem 1.5rem 2.5rem 0;
    margin: 0 auto;
    background: transparent;
    border: none;
    cursor: pointer;

    @include lg {
      font-size: 28px;
      padding: 25px 0;
    }

    @include md {
      font-size: 24px;
    }

    @include xs {
      font-size: 20px;
    }

    &::after {
      content: '>';
      margin: 0 0 0 2rem;
      flex-shrink: 0;
      font-size: 3.2rem;
      transform: rotate(90deg);
      transition: transform .3s;

      @include lg {
        margin: -5px 0 0 20px;
      }
    }
  }

  &__answer {
    max-height: 0;
    opacity: 0;
    overflow: hidden;
    will-change: max-height;
    transition: all 0.3s ease-out;
    font-size: 1.8rem;
    padding: 0 4.4rem;
    background: $colorWhite;

    @include lg {
      padding: 0 30px 0 40px;
    }

    @include md {
      font-size: 16px;
    }

    @include sm {
      font-size: 15px;
    }

    p {
      &:not(:last-of-type) {
        margin: 0 0 1.6rem;
      }
    }

    a {
      color: $colorBlack;
      transition: color .3s;

      &:hover {
        color: #d0c795;
      }
    }

    b {
      font-weight: 500;
    }
  }
}
