.stories {
  padding: 0 0 18rem;

  @include lg {
    padding: 0 0 90px;
  }

  @include xs {
    padding: 0 0 60px;
  }

  &__title {
    text-align: center;
    margin: 0 0 3.6rem;
  }

  &__wrap {
    display: flex;
    justify-content: space-between;

    @include md {
      display: block;
    }
  }

  &__item {
    width: calc(33.33% - 2.9rem * 2 / 3);

    @media (max-width: 1700px) {
      width: calc(33.33% - 1.5rem * 2 / 3);
    }

    @include md {
      max-width: 540px;
      width: 100%;
      margin: 0 auto;

      &:not(:last-of-type) {
        margin-bottom: 30px;
      }
    }
  }

  &__item-video {
    position: relative;
    display: block;
    height: 39.7rem;

    @media (max-width: 1700px) {
      height: 28rem;

      &:after {
        border-width: 3.5rem 0 3.5rem 6rem;
      }
    }

    @include md {
      height: 397px;
    }

    @include xs {
      height: 268px;
    }

    &:hover {
      &::after {
        transform: translate(-50%, -50%) scale(1.2);
      }
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(1);
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 5.5rem 0 5.5rem 9.6rem;
      border-color: transparent transparent transparent $colorWhite;
      transition: transform .6s;
    }
  }

  &__item-info {
    font-size: 1.8rem;
    background: $colorWhite;
    padding: 4rem 5rem 6rem 8rem;

    @media (max-width: 1700px) {
      padding: 3rem;
    }

    @include lg {
      padding: 30px 40px 40px;
    }
  }

  &__item-name {
    font-size: 3.6rem;
    font-family: $font-family-bodoni;
    line-height: 1.2;
    font-weight: 400;
    text-transform: uppercase;
    margin: 0 0 .8rem;

    @include lg {
      font-size: 32px;
      margin: 0 0 5px;
    }

    @include xs {
      font-size: 28px;
      margin: 0 0 2px;
    }
  }

  &__item-subname {
    color: #BDBDBD;
    margin: 0 0 1.3rem;

    @include lg {
      font-size: 16px;
    }

    @include xs {
      font-size: 14px;
      margin: 0 0 10px;
    }
  }

  &__item-text {
    font-size: 1.8rem;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 6;
    -webkit-box-orient: vertical;
    margin: 0 0 1.5rem;

    @include lg {
      font-size: 16px;
    }

    @include xs {
      font-size: 14px;
    }
  }

  &__item-btn {}

  &__wrap-btn {
    text-align: center;
  }

  &__more {
    min-height: 7rem;
    font-family: $font-family-bodoni;
    font-weight: 400;
    font-size: 2.4rem;
    background: transparent;
    text-transform: uppercase;
    margin: 6rem 0 0;
    color: $colorBlack;
    padding: 0 3.7rem;
    border: 1px solid #000000;
    transition: opacity .3s;

    &:hover {
      opacity: .5;
    }
  }
}
