.home-gallery {
  padding: 0 0 10rem;

  @include lg {
    padding: 0 0 70px;
  }

  @include xs {
    padding: 0 0 50px;
  }

  &__slider {
    margin: 0 0 7.5rem;

    .swiper-slide {
      width: auto;
      height: unset;
      display: inline-flex;
      align-items: center;
      justify-content: center;

      @media (min-width: 1700px) {
        &:first-of-type {
          margin-left: calc((100vw - 171rem) / 2);
        }

        &:last-of-type {
          margin-right: calc((100vw - 171rem) / 2);
        }
      }

      @media (max-width: 1700px) {
        &:first-of-type {
          margin-left: calc((100vw - 136rem) / 2);
        }

        &:last-of-type {
          margin-right: calc((100vw - 136rem) / 2);
        }
      }

      @include lg {
        &:first-of-type {
          margin-left: 25px;
        }

        &:last-of-type {
          margin-right: 25px;
        }
      }
    }
  }

  &__slide {
    max-width: 87.5rem;
    width: 100%;
    cursor: pointer;
  }

  &__scroll {}

}
