.title {
  font-family: $font-family-bodoni;
  font-weight: 400;
  font-size: 6.4rem;
  line-height: 1.2;
  text-transform: uppercase;

  @include lg {
    font-size: 54px;
  }

  &--big {
    font-size: 8rem;

    @include md {
      font-size: 48px;
    }

    @include xs {
      font-size: 44px;
    }
  }

  &--page {
    font-size: 4.8rem;

    @include md {
      font-size: 42px;
    }

    @include xs {
      font-size: 32px;
    }
  }
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;

  &--help {
    font-weight: 400;
    min-height: 6.7rem;
    color: $colorBlack;
    padding: 0 4.3rem;
    border: .7rem solid #D0C795;
    transition: color .3s;

    &:hover {
      color: #D0C795;
    }
  }
}

.slider-scroll {
  background: #BDB68C;
  border-radius: 9rem;
  height: .6rem;

  .swiper-scrollbar-drag {
    background: $colorWhite;
    border-radius: 9rem;
    height: 0.6rem;
  }
}

.loader {
  width: 5rem;
  height: 5rem;
  animation: load 2s linear 0s infinite;
}

.fancybox__content {
  padding: 0;
  background: transparent;
}

.fancybox__button--zoom {
  display: none;
}

@keyframes load {
  from {
    transform: translate(-50%, -50%) rotate(0deg);
  }

  to {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
