.header {
  padding: 1rem 0;
  background: $colorWhite;

  @include md {
    background: #D0C795;
  }

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include md {
      padding: 0 18px;
    }
  }

  &--home {
    padding: 0;
    background: transparent;

    @include lg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      padding: 12px 0;
    }

    .container {
      max-width: 100%;
      padding: 0;
      align-items: flex-start;

      @include lg {
        align-items: center;
        padding: 0 18px;
      }
    }

    .header__logo {
      width: 26.6rem;
      height: 8.8rem;

      @include lg {
        width: 194px;
        height: 65px;
      }

      @include xs {
        width: 137px;
        height: 45px;
      }
    }

    .header__nav-item:first-of-type {
      display: none;
    }
  }

  &__logo {
    width: 19.4rem;
    height: 6.5rem;

    @include xs {
      width: 137px;
      height: 45px;
    }
  }

  &__menu {
    @include xs {
      position: fixed;
      top: 0;
      right: 0;
      transform: translateX(-100%);
      width: 100%;
      height: 100%;
      background: #D0C795;
      padding: 50px 20px 20px;
      z-index: 5;
      transition: 0.3s;
      overflow: auto;

      &.active {
        transform: translateX(0);
      }
    }

    ul {
      display: flex;
      margin: 0 -5rem;

      @include md {
        margin: 0 -20px;
      }

      @include xs {
        display: block;
        margin: 0;
      }
    }
  }

  &__nav-item {
    padding: 0 5rem;

    @include md {
      padding: 0 20px;
    }

    @include xs {
      padding: 0;
    }
  }

  &__nav-link {
    font-size: 1.8rem;
    text-transform: lowercase;
    color: $colorBlack;
    transition: opacity .3s;

    &:hover {
      opacity: .5;
    }

    @include xs {
      font-size: 20px;
    }
  }

  &__burger {
    display: none;

    @include xs {
      display: flex;
      align-items: flex-end;
      justify-content: center;
      flex-direction: column;
      flex-shrink: 0;
      width: 30px;
      height: 22px;
      background: transparent;
      z-index: 6;
      cursor: pointer;
      border: none;
      position: relative;
      margin: 0 0 0 auto;

      span {
        background: $colorBlack;
        width: 100%;
        height: 2px;
        margin: 8px 0;
        border-radius: 10px;
        pointer-events: none;
        transition: 0.3s;
      }

      &::before,
      &::after {
        content: '';
        background: $colorBlack;
        width: 100%;
        height: 2px;
        border-radius: 10px;
        transition: 0.3s;
      }

      &.active {
        span {
          transform: scale(0);
        }

        &::before {
          position: absolute;
          top: 10px;
          transform: rotate(45deg);
        }

        &::after {
          position: absolute;
          bottom: 10px;
          transform: rotate(-45deg);
        }
      }
    }
  }
}
