.hero {
  padding: 4rem 6rem 10rem;

  @include lg {
    padding: 100px 30px 70px;
  }

  @include md {
    padding: 300px 18px 70px;
  }

  &__container {
    background: $colorWhite;
    border: 0.1rem solid $colorWhite;
    padding: 4rem 9rem 8.7rem 4.5rem;

    @include lg {
      padding: 30px 40px;
    }

    @include md {
      padding: 0 35px 50px;
    }

    @include xs {
      padding: 0 50px 50px 30px;
    }
  }

  &__content {
    max-width: 130rem;
    width: 100%;
    margin: -3rem 0 0 auto;

    @media (min-width: 2500px) {
      margin: -3rem auto 0;
    }

    @media (max-width: 1700px) {
      max-width: 120rem;
    }

    @include lg {
      margin: 0;
    }
  }

  &__wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 0 2.5rem;

    @include md {
      flex-direction: column-reverse;
      margin: 0 0 20px;
    }
  }

  &__info {
    max-width: 55rem;
    margin: 0 2.5rem 0 0;

    @include lg {
      max-width: 450px;
      width: 100%;
    }

    @include md {
      max-width: 100%;
      align-self: flex-start;
      margin: 0;
    }
  }

  &__title {
    margin: 0 0 1.7rem;

    @include md {
      line-height: 1;
      margin: 0 0 10px;
    }

    @include xs {
      font-size: 40px;
      letter-spacing: 0.04em;
    }
  }

  &__subtitle {
    font-size: 1.8rem;

    p {
      &:not(:last-of-type) {
        margin: 0 0 1.6rem;
      }
    }

    @include md {
      font-size: 16px;
    }

    @include sm {
      font-size: 15px;
    }
  }

  &__img {
    max-width: 57rem;

    @include lg {
      max-width: 470px;
    }

    @include md {
      max-width: 400px;
      margin: -240px 0 20px;
    }

    @include xs {
      max-width: 320px;
    }
  }

  &__footer {
    display: flex;
    align-items: flex-start;

    @include md {
      flex-wrap: wrap;
    }

    @include sm {
      display: block;
    }
  }

  &__footer-nums {
    display: flex;

    @include md {
      justify-content: center;
      width: 100%;
      margin: 0 0 25px;
    }

    @include xs {
      justify-content: flex-start;
    }
  }

  &__footer-nums-item {
    position: relative;
    font-family: $font-family-bodoni;
    line-height: 1;
    font-weight: 400;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    text-align: center;
    padding: 0 8rem 0 0;
    margin: 0 8rem 0 0;

    @include lg {
      font-size: 14px;
      padding: 0 40px 0 0;
      margin: 0 40px 0 0;
    }

    @include md {
      &:last-of-type {
        padding: 0;
        margin: 0;

        &::after {
          content: none;
        }
      }
    }

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 0;
      width: 0.1rem;
      height: 5rem;
      background: #D0C795;
    }
  }

  &__footer-nums-val {
    font-size: 9.6rem;

    @include lg {
      font-size: 72px;
    }

    @include sm {
      font-size: 54px;
    }
  }

  &__footer-sign {
    font-family: $font-family-bodoni;
    font-size: 4.8rem;
    line-height: 1.2;
    font-weight: 400;
    text-transform: uppercase;
    margin: 0 2rem 0 0;

    @include lg {
      font-size: 34px;
    }

    @include sm {
      margin: 0 0 10px;
    }

    @include xs {
      font-size: 30px;
    }
  }

  &__name {
    font-size: 1.8rem;
    align-self: flex-end;
    margin: 0 0 1rem auto;

    @include lg {
      font-size: 15px;
      margin: 0 0 15px auto;
    }

    @include sm {
      margin: 0;
    }
  }
}
